import React from 'react';
import ReactDOM from 'react-dom';
import thunk from 'redux-thunk';
import shortid from 'shortid';
import App from './components/App';
import { createStore, applyMiddleware } from 'redux';
import { Provider } from 'react-redux';
import createLogger from 'redux-logger';
import rootReducer from './reducers/rootReducer';
import {
  canMoveInDirection,
  npcIdOfPokemonPlayerIsEating,
  speciesIdOfPokemonPlayerIsEating,
  isOnTopOfBerry,
  isOnTopOfMushroom,
  isOnTopOfGem,
  isOnTopOfPokemon,
  getRandomReachableUnoccupedTile,
  getBerrySpawnTile,
  getMushroomSpawnTile,
  getGemSpawnTile,
  getPokemonSpawnTile,
  alreadyIsAGem,
  playerCanEvolve,
  nextSpeciesInEvolutionSequence,
  howManyBerries,
  howManyMushrooms,
  howManyPokemon,
  getDamageCausedForSpeciesId,
  getHomingDirection,
  getDistanceFromPlayer,
  isPlayerHidden,
  isTotalPokemonCapturedMutlipleOfN,
} from './selectors';
import getRandomPokemonSpecies from './tilemap/pokemonRarity';
import './index.css';

const store = createStore(rootReducer, applyMiddleware(thunk));

ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>,
  document.getElementById('root')
);

window.addEventListener('keydown', e => {
  switch (e.which) {
    case 37:
      e.preventDefault();
      store.dispatch(move('left'));
      break;
    case 38:
      e.preventDefault();
      store.dispatch(move('up'));
      break;
    case 39:
      e.preventDefault();
      store.dispatch(move('right'));
      break;
    case 40:
      e.preventDefault();
      store.dispatch(move('down'));
      break;
    default:
      break;
  }
});

function move(direction) {
  return function(dispatch, getState){
    dispatch({
      type: 'MOVE',
      direction,
    });

    if (isOnTopOfGem(getState())) {
      dispatch({
        type: 'EAT_GEM',
      });

      if (playerCanEvolve(getState())) {
        dispatch({
          type: 'ADD_TO_POKEDEX',
          speciesId: nextSpeciesInEvolutionSequence(getState()),
        }),

        dispatch({
          type: 'CHANGE_PLAYER_SPECIES',
          speciesId: nextSpeciesInEvolutionSequence(getState()),
        });
      }
    }

    if (isOnTopOfBerry(getState())) {
      dispatch({type: 'EAT_BERRY'});
      /*
      if (getState().player.hp === 20 && !alreadyIsAGem(store.getState())) {
        const unoccupiedTileForGem = getGemSpawnTile(store.getState());
        store.dispatch({
          type: 'SPAWN_GEM',
          x: unoccupiedTileForGem.x,
          y: unoccupiedTileForGem.y,
        });
      }
      */
    }

    if (isOnTopOfMushroom(getState())) dispatch({type: 'EAT_MUSHROOM'});

    if (isOnTopOfPokemon(getState())) {
      const npcIdOfPokemon = npcIdOfPokemonPlayerIsEating(getState());
      const speciesIdOfPokemon = speciesIdOfPokemonPlayerIsEating(getState());
      const damage = getDamageCausedForSpeciesId(getState(), speciesIdOfPokemon);
      dispatch({
        type: 'EAT_POKEMON',
        npcId: npcIdOfPokemon,
        speciesId: speciesIdOfPokemon,
        damage,
      });

      if (isTotalPokemonCapturedMutlipleOfN(getState(), 10) && !alreadyIsAGem(getState())) {
        const unoccupiedTileForGem = getGemSpawnTile(store.getState());
        store.dispatch({
          type: 'SPAWN_GEM',
          x: unoccupiedTileForGem.x,
          y: unoccupiedTileForGem.y,
        });
      }
    }

    if (store.getState().player.hp <= 0) {
      dispatch({
        type: 'RESPAWN_PLAYER',
      });
    }

  }
}

window.setInterval(() => {
  if (howManyBerries(store.getState()) < 5) {
    const unoccupiedTileForBerry = getBerrySpawnTile(store.getState());
    store.dispatch({
      type: 'SPAWN_BERRY',
      x: unoccupiedTileForBerry.x,
      y: unoccupiedTileForBerry.y,
    });
  }

  if (howManyMushrooms(store.getState()) < 10) {
    const unoccupiedTileForMushroom = getMushroomSpawnTile(store.getState());
    store.dispatch({
      type: 'SPAWN_MUSHROOM',
      x: unoccupiedTileForMushroom.x,
      y: unoccupiedTileForMushroom.y,
    });
  }

}, 1000);

window.setInterval(function(){
  if (howManyPokemon(store.getState()) < 5) {
    const unoccupiedTileForPokemon = getPokemonSpawnTile(store.getState());
    const npcId = shortid.generate();
    store.dispatch({
      type: 'SPAWN_POKEMON',
      npcId,
      x: unoccupiedTileForPokemon.x,
      y: unoccupiedTileForPokemon.y,
      speciesId: getRandomPokemonSpecies(),
    });
  }
}, 300);

window.setInterval(function(){
  store.getState().npcs.forEach(npc => {
    let direction;

    if (
      isPlayerHidden(store.getState())
      || getDistanceFromPlayer(store.getState(), npc) > 3
    ) {
      const ran = Math.ceil(Math.random() * 4);
      if (ran === 1) direction = 'left';
      else if (ran === 2) direction = 'up';
      else if (ran === 3) direction = 'down';
      else if (ran === 4) direction = 'right';
    } else {
      direction = getHomingDirection(store.getState(), npc);
    }

    store.dispatch({
      type: 'MOVE_NPC',
      npcId: npc.id,
      direction,
    });
  });

  if (isOnTopOfPokemon(store.getState())) {
    const npcIdOfPokemon = npcIdOfPokemonPlayerIsEating(store.getState());
    const speciesIdOfPokemon = speciesIdOfPokemonPlayerIsEating(store.getState());
    const damage = getDamageCausedForSpeciesId(store.getState(), speciesIdOfPokemon);
    store.dispatch({
      type: 'EAT_POKEMON',
      npcId: npcIdOfPokemon,
      speciesId: speciesIdOfPokemon,
      damage,
    });

    if (isTotalPokemonCapturedMutlipleOfN(store.getState(), 10) && !alreadyIsAGem(store.getState())) {
      const unoccupiedTileForGem = getGemSpawnTile(store.getState());
      store.dispatch({
        type: 'SPAWN_GEM',
        x: unoccupiedTileForGem.x,
        y: unoccupiedTileForGem.y,
      });
    }
  }

  if (store.getState().player.hp <= 0) {
    store.dispatch({
      type: 'RESPAWN_PLAYER',
    });
  }
}, 1000);



