import { canMoveInDirection, canNpcMoveInDirection } from '../selectors';
import tilemap from '../tilemap/tilemap';
import {
  berrySpawnRate,
  gemSpawnRate,
  mushroomSpawnRate
} from '../tilemap/spawnRates';
import hiddenInfo from '../tilemap/hiddenInfo';
import movementInfo from '../tilemap/movementInfo';

const initialState = [];

tilemap.forEach((layer, z) =>
  layer.forEach((row, y) =>
    row.forEach((tileId, x) => {
      const tileIfExists = initialState.find(tile => tile.x === x && tile.y === y)
      if (!tileIfExists && tileId !== null) {
        initialState.push({
          x,
          y,
          tiles: [tileId],
          player: false,
          berry: false,
          mushroom: false,
          gem: false,
          movementInfo: movementInfo(tileId),
          playerHidden: hiddenInfo(tileId),
          berrySpawnRate: berrySpawnRate(tileId),
          mushroomSpawnRate: mushroomSpawnRate(tileId),
          gemSpawnRate: gemSpawnRate(tileId),
          npc: false,
        });
      } else if (tileId !== null) {

        tileIfExists.tiles.push(tileId);

        const {
          left: oldLeft,
          up: oldUp,
          right: oldRight,
          down: oldDown
        } = tileIfExists.movementInfo;

        const {
          left: newLeft,
          up: newUp,
          right: newRight,
          down: newDown
        } = movementInfo(tileId);

        tileIfExists.movementInfo = {
          left: oldLeft && newLeft,
          up: oldUp && newUp,
          right: oldRight && newRight,
          down: oldDown && newDown,
        };

        tileIfExists.playerHidden = tileIfExists.playerHidden || hiddenInfo(tileId);

        tileIfExists.berrySpawnRate = Math.max(tileIfExists.berrySpawnRate, berrySpawnRate(tileId));

        tileIfExists.gemSpawnRate = Math.max(tileIfExists.gemSpawnRate, gemSpawnRate(tileId));

        tileIfExists.mushroomSpawnRate = Math.max(tileIfExists.mushroomSpawnRate, mushroomSpawnRate(tileId));
      }
    })
  )
);

initialState.find(tile => tile.x === 5 && tile.y === 3).player = true;
initialState.find(tile => tile.x === 6 && tile.y === 3).berry = true;
initialState.find(tile => tile.x === 6 && tile.y === 15).berry = true;

export default function(state = initialState, action) {
  if (action.type === 'EAT_BERRY') {
    const newState = state.slice();
    const playerTileI = state.findIndex(tile => tile.player);
    newState[playerTileI] = Object.assign(
      {},
      newState[playerTileI],
      {berry: false}
    );
    return newState;
  }

  if (action.type === 'EAT_MUSHROOM') {
    const newState = state.slice();
    const playerTileI = state.findIndex(tile => tile.player);
    newState[playerTileI] = Object.assign(
      {},
      newState[playerTileI],
      {mushroom: false}
    );
    return newState;
  }

  if (action.type === 'EAT_GEM') {
    const newState = state.slice();
    const playerTileI = state.findIndex(tile => tile.player);
    newState[playerTileI] = Object.assign(
      {},
      newState[playerTileI],
      {gem: false}
    );
    return newState;
  }

  if (action.type === 'EAT_POKEMON') {
    const newState = state.slice();
    const playerTileI = state.findIndex(tile => tile.player);
    newState[playerTileI] = Object.assign(
      {},
      newState[playerTileI],
      {npc: false}
    );
    return newState;
  }

  if (action.type === 'SPAWN_BERRY') {
    const newState = state.slice();
    const {x, y} = action;
    const oldTileI = state.findIndex(tile => tile.x === x && tile.y === y);

    newState[oldTileI] = Object.assign(
      {},
      newState[oldTileI],
      {berry: true}
    );

    return newState;
  }

  if (action.type === 'SPAWN_MUSHROOM') {
    const newState = state.slice();
    const {x, y} = action;
    const oldTileI = state.findIndex(tile => tile.x === x && tile.y === y);

    newState[oldTileI] = Object.assign(
      {},
      newState[oldTileI],
      {mushroom: true}
    );

    return newState;
  }

  if (action.type === 'SPAWN_GEM') {
    const newState = state.slice();
    const {x, y} = action;
    const oldTileI = state.findIndex(tile => tile.x === x && tile.y === y);

    newState[oldTileI] = Object.assign(
      {},
      newState[oldTileI],
      {gem: true}
    );

    return newState;
  }

  if (action.type === 'SPAWN_POKEMON') {
    const newState = state.slice();
    const {x, y, npcId} = action;
    const tileI = state.findIndex(tile => tile.x === x && tile.y === y);

    newState[tileI] = Object.assign(
      {},
      newState[tileI],
      {npc: npcId}
    );

    return newState;
  }

  if (action.type === 'MOVE') {
    if (!canMoveInDirection({tiles: state}, action.direction)) return state;

    const newState = state.slice();
    const oldTileI = state.findIndex(tile => tile.player);

    let newTileI;

    if (action.direction === 'left') {
      newTileI = state.findIndex(tile =>
        tile.x === newState[oldTileI].x - 1
        &&
        tile.y === newState[oldTileI].y
      );
    } else if (action.direction === 'up') {
      newTileI = state.findIndex(tile =>
        tile.x === newState[oldTileI].x
        &&
        tile.y === newState[oldTileI].y - 1
      );
    } else if (action.direction === 'right') {
      newTileI = state.findIndex(tile =>
        tile.x === newState[oldTileI].x + 1
        &&
        tile.y === newState[oldTileI].y
      );
    } else if (action.direction === 'down') {
      newTileI = state.findIndex(tile =>
        tile.x === newState[oldTileI].x
        &&
        tile.y === newState[oldTileI].y + 1
      );
    }

    newState[oldTileI] = Object.assign(
      {},
      newState[oldTileI],
      {player: false}
    );

    newState[newTileI] = Object.assign(
      {},
      newState[newTileI],
      {player: true}
    );

    return newState;
  }

  if (action.type === 'MOVE_NPC') {
    if (!canNpcMoveInDirection({tiles: state}, action.npcId, action.direction)) return state;
    
    const newState = state.slice();
    const oldTileI = state.findIndex(tile => tile.npc === action.npcId);

    let newTileI;

    if (action.direction === 'left') {
      newTileI = state.findIndex(tile =>
        tile.x === newState[oldTileI].x - 1
        &&
        tile.y === newState[oldTileI].y
      );
    } else if (action.direction === 'up') {
      newTileI = state.findIndex(tile =>
        tile.x === newState[oldTileI].x
        &&
        tile.y === newState[oldTileI].y - 1
      );
    } else if (action.direction === 'right') {
      newTileI = state.findIndex(tile =>
        tile.x === newState[oldTileI].x + 1
        &&
        tile.y === newState[oldTileI].y
      );
    } else if (action.direction === 'down') {
      newTileI = state.findIndex(tile =>
        tile.x === newState[oldTileI].x
        &&
        tile.y === newState[oldTileI].y + 1
      );
    }

    newState[oldTileI] = Object.assign(
      {},
      newState[oldTileI],
      {npc: false}
    );

    newState[newTileI] = Object.assign(
      {},
      newState[newTileI],
      {npc: action.npcId}
    );

    return newState;
  }

  if (action.type === 'RESPAWN_PLAYER') {
    const newState = state.slice();
    const oldTileI = state.findIndex(tile => tile.player);
    const newTileI = state.findIndex(tile => tile.x === 5 && tile.y === 3);
    newState[oldTileI] = Object.assign(
      {},
      newState[oldTileI],
      {player: false}
    );

    newState[newTileI] = Object.assign(
      {},
      newState[newTileI],
      {player: true}
    );

    return newState;
  }

  return state;
}



