import pokemonSoFar from '../tilemap/pokemonSoFar';

const initialState = {
  facing: 'right',
  hp: 15,
  speciesId: 1,
  pokedex: {},
  totalPokemonCaptured: 0,
}

pokemonSoFar.forEach(id => initialState.pokedex[id] = 0);
initialState.pokedex[1] = 1;

export default function(state = initialState, action) {
  if (action.type === 'MOVE') {
    switch (action.direction) {
      case 'left':
        return Object.assign(
          {},
          state,
          {
            facing: 'left',
          }
        );
      case 'up':
        return state;
      case 'right':
        return Object.assign(
          {},
          state,
          {
            facing: 'right',
          }
        );
      case 'down':
        return state;
      default:
          break;
    }
  }

  if (action.type === 'CASH_IN_POKEMON') {
    const {speciesId, evolutionId} = action;

    console.log(speciesId, evolutionId);

    return Object.assign(
      {},
      state,
      {
        pokedex: Object.assign(
          {},
          state.pokedex,
          {

            [speciesId]: state.pokedex[speciesId] - 3,
            [evolutionId]: (state.pokedex[evolutionId] || 0) + 1,
          }
        ),
      }
    );
  }

  if (action.type === 'EAT_BERRY') {
    return Object.assign(
      {},
      state,
      {
        hp: Math.min(20, state.hp + 1),
      }
    );
  }

  if (action.type === 'EAT_MUSHROOM') {
    return Object.assign(
      {},
      state,
      {
        hp: Math.max(state.hp - 1, 0),
      }
    );
  }

  if (action.type === 'EAT_POKEMON' || action.type === 'ADD_TO_POKEDEX') {
    return Object.assign(
      {},
      state,
      {
        hp: (action.damage) ? state.hp - action.damage : state.hp,
        totalPokemonCaptured: state.totalPokemonCaptured + 1,
        pokedex: Object.assign(
          {},
          state.pokedex,
          {
            [action.speciesId]: (state.pokedex[action.speciesId] || 0) + 1,
          }
        ),
      }
    );
  }

  if (action.type === 'CHANGE_PLAYER_SPECIES') {
    return Object.assign(
      {},
      state,
      {speciesId: action.speciesId}
    );
  }

  if (action.type === 'RESPAWN_PLAYER') {
    return initialState;
  }

  return state;
}



