import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getUniqueSpeciesOnMap, getDmgForSpecies } from '../selectors';
import DmgGuide from './DmgGuide';
import { widthOfHpBarInPx } from '../shareableCSS';

function genHPBlocks(hp) {
  const result = [];
  let bgColor;
  if (hp > 8) {
    bgColor = '#090';
  } else if (hp > 4) {
    bgColor = '#963';
  } else {
    bgColor = '#933';
  }

  let boxShadowColor;
  if (hp > 8) {
    boxShadowColor = '#060';
  } else if (hp > 4) {
    boxShadowColor = '#630';
  } else {
    boxShadowColor = '#600';
  }

  boxShadowColor = '#333'

  for (let i = 1; i <= 20; i++) {
    let filledIn;
    if (i + hp > 20) {
      filledIn = true;
    } else {
      filledIn = false;
    }
    result.push(
      <div
        key={i}
        style={{
          backgroundClip: 'content-box',
          backgroundColor: filledIn ? bgColor : 'transparent',
          border: '1px solid transparent',
          boxShadow: `${boxShadowColor} 0 0 5px inset`,
          height: '32px',
          marginLeft: (widthOfHpBarInPx - 32) + 'px',
          opacity: 1,
          width: '32px',
        }}
      />
    );
  }
  return result;
}

class HPBar extends Component {
  render(){
    const numberAtDmgLevel = [];
    const dmgGuides = this.props.speciesAndDmgInfo.map(unit => {
      numberAtDmgLevel[unit.damage] =
        numberAtDmgLevel[unit.damage]
        ? numberAtDmgLevel[unit.damage] + 1
        : 1;

      return (
        <DmgGuide
          key={unit.speciesId}
          speciesId={unit.speciesId}
          damage={unit.damage}
          numberAtDmgLevel={numberAtDmgLevel[unit.damage]}
        />
      );
    });

    return (
      <div style={{
        float: 'left',
        height: '640px',
        position: 'relative',
        width: widthOfHpBarInPx + 'px',
      }}>
        {dmgGuides}
        {genHPBlocks(this.props.hp)}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  hp: state.player.hp,
  speciesAndDmgInfo: getUniqueSpeciesOnMap(state).map(
    speciesId => ({
      speciesId,
      damage: getDmgForSpecies(state, speciesId),
    })
  ),
});

export default connect(mapStateToProps)(HPBar);



