import React, { Component } from 'react';
import { connect } from 'react-redux';
import { evolutionOfPokemon, speciesCanEvolve } from '../selectors';
import { widthOfPokedexInPx } from '../shareableCSS';

function Pokedex({ dispatch, pokedex, pokemonSpecies, state }) {
  const squares = [];

  for (let i = 0; i < 151; i++) {
    const species = pokemonSpecies.find(species => species.id === i + 1);
    const canCapture = species !== undefined;
    let backgroundPosition;
    let backgroundSize;
    let img;
    let hasCaptured;

    if (canCapture) {
      img = require('../../imgs/' + species.img);
      backgroundPosition = species.backgroundPosition;
      backgroundSize = species.backgroundSize;
      hasCaptured = pokedex[species.id] > 0;
    }

    const top = Math.floor(i / 7) * 36;
    const left = (i - Math.floor(i / 7) * 7) * 36;

    const capturedThreeOrMore = hasCaptured && pokedex[species.id] >= 3;

    const thisSpeciesCanEvolve = canCapture && speciesCanEvolve(state, species.id);

    squares.push(
      <div
        key={i}
        style={{
          backgroundColor: '#f8f8f8',
          backgroundImage: canCapture && `url(${img})`,
          backgroundPosition,
          backgroundSize,
          border: '1px solid #999',
          boxSizing: 'border-box',
          height: '32px',
          left: left + 'px',
          opacity: (canCapture && !hasCaptured) ? '0.3' : '1',
          position: 'absolute',
          top: top + 'px',
          WebkitFilter: (canCapture && !hasCaptured) ? 'grayscale(500%)' : '',
          width: '32px',
        }}
        onClick={hasCaptured ? () => dispatch({
            type: 'CHANGE_PLAYER_SPECIES',
            speciesId: species.id,
          }) : () => {}}
      >
        {hasCaptured ? <div style={{
            backgroundColor: (capturedThreeOrMore && thisSpeciesCanEvolve) ? 'rgba(255, 100, 100, 0.85)' : 'rgba(255, 255, 255, 0.85)',
            bottom: '1px',
            borderRadius: '14px',
            cursor: (capturedThreeOrMore && thisSpeciesCanEvolve) ? 'pointer' : 'default',
            fontSize: '12px',
            fontWeight: 600,
            height: '14px',
            lineHeight: '14px',
            position: 'absolute',
            right: '1px',
            textAlign: 'center',
            width: '14px',
          }}
          onClick={(capturedThreeOrMore && thisSpeciesCanEvolve) ? e => {
            e.stopPropagation();
            dispatch({
              type: 'CASH_IN_POKEMON',
              speciesId: species.id,
              evolutionId: evolutionOfPokemon(state, species.id),
            });
          } : () => {}}
          >{species && pokedex[species.id]} </div>: ''}
      </div>
    );
  }

  return (
    <div style={{
      height: '500px',
      position: 'relative',
      width: widthOfPokedexInPx + 'px',
      float: 'right',
    }}>
      {squares}
    </div>
  );
}

const mapStateToProps = state => ({
  state: state,
  pokedex: state.player.pokedex,
  pokemonSpecies: state.pokemonSpecies,
});

const mapDispatchToProps = dispatch => ({
  dispatch
});

export default connect(mapStateToProps, mapDispatchToProps)(Pokedex);



