import { pokemonSpawnRate } from './tilemap/spawnRates';

function getAllUnoccupiedTiles(tiles) {
  return tiles.filter(tile =>
    !tile.berry &&
    !tile.mushroom &&
    !tile.npc &&
    !tile.player
  );
}

function isUnoccupiedByNPC(tile){
  return !tile.npc;
}

function isUnoccupiedByPlayer(tile){
  return !tile.player;
}

function isTileReachable(tile){
  return tile.movementInfo.left ||
    tile.movementInfo.up ||
    tile.movementInfo.right ||
    tile.movementInfo.down;
}

function getReachableUnoccupiedTiles(tiles) {
  const unoccupiedTiles = getAllUnoccupiedTiles(tiles);
  return unoccupiedTiles.filter(tile => isTileReachable(tile));
}

export function getRandomReachableUnoccupedTile({tiles}) {
  const reachableUnoccupiedTiles = getReachableUnoccupiedTiles(tiles);
  const randomIndex = Math.ceil(Math.random() * reachableUnoccupiedTiles.length);
  return reachableUnoccupiedTiles[randomIndex];
}

function getRandomArrayElemWithWeights(items, weights) {
  const totalWeight = weights.reduce((prev, cur) => prev + cur);
  const normalizedWeights = weights.map(weight => weight / totalWeight);

  const ran = Math.random();
  let s = 0;
  for (let i = 0; i < items.length - 1; i++) {
    s += normalizedWeights[i];
    if (ran < s) return items[i];
  }
  return items[items.length - 1];
}

export function getBerrySpawnTile({tiles}) {
  const reachableUnoccupiedTiles = getReachableUnoccupiedTiles(tiles);
  const reachableUnoccupiedTilesWithChance = reachableUnoccupiedTiles.filter(tile => tile.berrySpawnRate > 0)
  const probabilityWeights = reachableUnoccupiedTilesWithChance.map(tile => tile.berrySpawnRate);
  return getRandomArrayElemWithWeights(reachableUnoccupiedTilesWithChance, probabilityWeights)
}

export function getMushroomSpawnTile({tiles}) {
  const reachableUnoccupiedTiles = getReachableUnoccupiedTiles(tiles);
  const reachableUnoccupiedTilesWithChance = reachableUnoccupiedTiles.filter(tile => tile.mushroomSpawnRate > 0)
  const probabilityWeights = reachableUnoccupiedTilesWithChance.map(tile => tile.mushroomSpawnRate);
  return getRandomArrayElemWithWeights(reachableUnoccupiedTilesWithChance, probabilityWeights)
}

export function getGemSpawnTile({tiles}) {
  const reachableUnoccupiedTiles = getReachableUnoccupiedTiles(tiles);
  const reachableUnoccupiedTilesWithChance = reachableUnoccupiedTiles.filter(tile => tile.gemSpawnRate > 0)
  const probabilityWeights = reachableUnoccupiedTilesWithChance.map(tile => tile.gemSpawnRate);
  return getRandomArrayElemWithWeights(reachableUnoccupiedTilesWithChance, probabilityWeights)
}

function getPokemonSpawnRate(tile, speciesId) {
  return tile.tiles.reduce((p, c) => pokemonSpawnRate(c));
}

export function getPokemonSpawnTile({tiles}, speciesId) {
  const reachableUnoccupiedTiles = getReachableUnoccupiedTiles(tiles);
  const reachableUnoccupiedTilesWithChance = reachableUnoccupiedTiles.filter(tile => getPokemonSpawnRate(tile) > 0)
  const probabilityWeights = reachableUnoccupiedTilesWithChance.map(tile => getPokemonSpawnRate(tile));
  return getRandomArrayElemWithWeights(reachableUnoccupiedTilesWithChance, probabilityWeights)
}

export function canMoveInDirection(state, direction) {
  const { tiles } = state;
  const oldTileI = tiles.findIndex(tile => tile.player);

  let newTileI;

  if (direction === 'left') {
    if (tiles[oldTileI].x === 0) return false;

    newTileI = tiles.findIndex(tile =>
      tile.x === tiles[oldTileI].x - 1
      &&
      tile.y === tiles[oldTileI].y
    );
  } else if (direction === 'up') {
    if (tiles[oldTileI].y === 0) return false;

    newTileI = tiles.findIndex(tile =>
      tile.x === tiles[oldTileI].x
      &&
      tile.y === tiles[oldTileI].y - 1
    );
  } else if (direction === 'right') {
    if (tiles[oldTileI].x === 29) return false;

    newTileI = tiles.findIndex(tile =>
      tile.x === tiles[oldTileI].x + 1
      &&
      tile.y === tiles[oldTileI].y
    );
  } else if (direction === 'down') {
    if (tiles[oldTileI].y === 19) return false;

    newTileI = tiles.findIndex(tile =>
      tile.x === tiles[oldTileI].x
      &&
      tile.y === tiles[oldTileI].y + 1
    );
  }

  return tiles[newTileI].movementInfo[direction];
}

export function canNpcMoveInDirection(state, npcId, direction) {
  const { tiles } = state;
  const oldTileI = tiles.findIndex(tile => tile.npc === npcId);

  let newTileI;

  if (direction === 'left') {
    if (tiles[oldTileI].x === 0) return false;

    newTileI = tiles.findIndex(tile =>
      tile.x === tiles[oldTileI].x - 1
      &&
      tile.y === tiles[oldTileI].y
    );
  } else if (direction === 'up') {
    if (tiles[oldTileI].y === 0) return false;

    newTileI = tiles.findIndex(tile =>
      tile.x === tiles[oldTileI].x
      &&
      tile.y === tiles[oldTileI].y - 1
    );
  } else if (direction === 'right') {
    if (tiles[oldTileI].x === 29) return false;

    newTileI = tiles.findIndex(tile =>
      tile.x === tiles[oldTileI].x + 1
      &&
      tile.y === tiles[oldTileI].y
    );
  } else if (direction === 'down') {
    if (tiles[oldTileI].y === 19) return false;

    newTileI = tiles.findIndex(tile =>
      tile.x === tiles[oldTileI].x
      &&
      tile.y === tiles[oldTileI].y + 1
    );
  }

  return (
    isUnoccupiedByNPC(tiles[newTileI])
    && tiles[newTileI].movementInfo[direction]
    && (isPlayerHidden(state) ? isUnoccupiedByPlayer(tiles[newTileI]) : true)
  );
}

export function isOnTopOfBerry({tiles}) {
  const tile = tiles.find(tile => tile.player);
  return tile.berry;
}

export function isOnTopOfMushroom({tiles}) {
  const tile = tiles.find(tile => tile.player);
  return tile.mushroom;
}

export function isOnTopOfGem({tiles}) {
  const tile = tiles.find(tile => tile.player);
  return tile.gem;
}

export function isOnTopOfPokemon({tiles}) {
  const tile = tiles.find(tile => tile.player);
  return tile.npc;
}

export function npcIdOfPokemonPlayerIsEating({tiles}) {
  const tile = tiles.find(tile => tile.player);
  return tile.npc;
}

export function speciesIdOfPokemonPlayerIsEating({npcs, tiles}) {
  const npcId = npcIdOfPokemonPlayerIsEating({tiles})
  return npcs.find(npc => npc.id === npcId).speciesId;
}

export function alreadyIsAGem({tiles}) {
  const tile = tiles.find(tile => tile.gem);
  return tile !== undefined;
}

export function playerCanEvolve(state){
  const curSpecies = state.pokemonSpecies.find(species => species.id === state.player.speciesId);
  return curSpecies.evolution;
}

export function speciesCanEvolve(state, speciesId){
  return state.pokemonSpecies.find(species => species.id === speciesId).evolution !== undefined;
}

export function nextSpeciesInEvolutionSequence(state){
  const curSpecies = state.pokemonSpecies.find(species => species.id === state.player.speciesId);
  return curSpecies.evolution;
}

export function evolutionOfPokemon(state, speciesId){
  return state.pokemonSpecies.find(species => species.id === speciesId).evolution;
}

export function howManyBerries({tiles}) {
  return tiles.filter(tile => tile.berry).length;
}

export function howManyMushrooms({tiles}) {
  return tiles.filter(tile => tile.mushroom).length;
}

export function howManyPokemon({tiles}) {
  return tiles.filter(tile => tile.npc).length;
}

export function getDamageCausedForSpeciesId(state, speciesId) {
  return state.pokemonSpecies.find(species => species.id === speciesId).damage;
}

export function getHomingDirection(state, npc) {
  const npcTile = state.tiles.find(tile => tile.npc === npc.id);
  const npcX = npcTile.x;
  const npcY = npcTile.y;

  const playerTile = state.tiles.find(tile => tile.player);
  const playerX = playerTile.x;
  const playerY = playerTile.y;

  const playerIsAbove = playerY < npcY;
  const playerIsBelow = playerY > npcY;

  const playerIsToLeft = playerX < npcX;
  const playerIsToRight = playerX > npcX;

  if (!playerIsToLeft && !playerIsToRight) {
    return playerIsAbove ? 'up' : 'down';
  }

  if (!playerIsAbove && !playerIsBelow) {
    return playerIsToLeft ? 'left' : 'right';
  }

  const ran = Math.ceil(Math.random() * 2);

  if (ran === 1) return playerIsAbove ? 'up' : 'down';
  return playerIsToLeft ? 'left' : 'right';
}

export function getDistanceFromPlayer(state, npc) {
  const npcTile = state.tiles.find(tile => tile.npc === npc.id);
  const npcX = npcTile.x;
  const npcY = npcTile.y;

  const playerTile = state.tiles.find(tile => tile.player);
  const playerX = playerTile.x;
  const playerY = playerTile.y;

  const xDistance = Math.abs(npcX - playerX);
  const yDistance = Math.abs(npcY - playerY);

  return Math.min(xDistance, yDistance);
}

export function isPlayerHidden({tiles}) {
  return tiles.find(tile => tile.player).playerHidden;
}

export function getUniqueSpeciesOnMap(state) {
  return state.tiles
    .filter(tile => tile.npc)
    .map(tile => tile.npc)
    .map(npcId => state.npcs.find(npc => npc.id === npcId).speciesId)
    .filter((speciesId, i, arr) => arr.indexOf(speciesId) === i);
}

export function getDmgForSpecies(state, speciesId) {
  return state.pokemonSpecies.find(species => species.id === speciesId).damage;
}

export function isTotalPokemonCapturedMutlipleOfN(state, n) {
  return state.player.totalPokemonCaptured % n === 0;
}



