import React, { Component } from 'react';
import { connect } from 'react-redux';
import Tile from './Tile';
import Pokemon from './Pokemon';
import Item from './Item';
import { widthOfWorldMapInPx } from '../shareableCSS';

import tilemap from '../tilemap/tilemap';

const tilemapArr = tilemap.map((layer, z) =>
    layer.map((row, y) =>
      row.map((tileId, x) =>
        (tileId === null)
        ?
        null
        :
        <Tile
          key={[x, y, z]}
          tileId={tileId}
          x={x}
          y={y}
          z={z}
        />
      )
    )
  ).filter(tile => tile !== null);

class WorldMap extends Component {
  render() {
    let {x, y, facing, berries, mushrooms, gem, hidden, species, npcs, layoutStyle, isZoomed} = this.props;

    let mapHorizontalOffset = 0;
    if (x > 7 && x < 20) {
      mapHorizontalOffset = x - 7;
    } else if (x >= 20) {
      mapHorizontalOffset = 12;
    }

    let mapVerticalOffset = 0;
    if (y > 5 && y < 13) {
      mapVerticalOffset = y - 5;
    } else if (y >= 13) {
      mapVerticalOffset = 8;
    }
  
    return (
      <div style={Object.assign({
        float: 'right',
        height: '640px',
        position: 'relative',
        overflow: 'hidden',
        width: widthOfWorldMapInPx + 'px',
      }, layoutStyle)}>
        <div style={{
          transform: isZoomed ? `scale(2, 2) translate(${widthOfWorldMapInPx/4 - (mapHorizontalOffset * 32)}px, -${mapVerticalOffset * 32}px)` : '',
          width: widthOfWorldMapInPx + 'px',
        }}>
          {tilemapArr}
          <Pokemon x={x} y={y} z={2} facing={facing} hidden={hidden} species={species} isPlayer={true} />

          {berries.map(({x, y, itemType}) =>
            <Item key={[x, y]} x={x} y={y} z={2} itemType={itemType} />
          )}

          {mushrooms.map(({x, y, itemType}) =>
            <Item key={[x, y]} x={x} y={y} z={2} itemType={itemType} />
          )}

          {gem.map(({x, y, itemType}) =>
            <Item key={[x, y]} x={x} y={y} z={2} itemType={itemType} />
          )}

          {npcs.map(({id, x, y, facing, species}) =>
            <Pokemon key={id} x={x} y={y} z={2} facing={facing} species={species} />
          )}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const tileWithPlayer = state.tiles.find(tile => tile.player);

  const berries = state.tiles
    .filter(tile => tile.berry)
    .map(tile => ({
      x: tile.x,
      y: tile.y,
      itemType: state.items.find(item => item.id === 1),
    }));

  const mushrooms = state.tiles
    .filter(tile => tile.mushroom)
    .map(tile => ({
      x: tile.x,
      y: tile.y,
      itemType: state.items.find(item => item.id === 2),
    }));

  const gem = state.tiles
    .filter(tile => tile.gem)
    .map(tile => ({
      x: tile.x,
      y: tile.y,
      itemType: state.items.find(item => item.id === 3),
    }));

  const npcs = state.tiles.filter(tile => tile.npc).map(tile => {
    const npc = state.npcs.find(npc => npc.id === tile.npc);
    const species = state.pokemonSpecies.find(species => species.id === npc.speciesId);

    return Object.assign(
      {},
      {
        id: npc.id,
        x: tile.x,
        y: tile.y,
        facing: npc.facing,
        species,
      }
    )
  });

  return {
    berries,
    mushrooms,
    gem,
    x: tileWithPlayer.x,
    y: tileWithPlayer.y,
    facing: state.player.facing,
    hidden: tileWithPlayer.playerHidden,
    species: state.pokemonSpecies.find(species => species.id === state.player.speciesId),
    npcs,
  }
};

export default connect(mapStateToProps)(WorldMap);



