import { combineReducers } from 'redux';
import items from './items';
import npcs from './npcs';
import player from './player';
import pokemonSpecies from './pokemonSpecies';
import tiles from './tiles';

export default combineReducers({
  items,
  npcs,
  player,
  pokemonSpecies,
  tiles,
});



