import React, { Component } from 'react';
import { connect } from 'react-redux';
import HPBar from './HPBar';
import WorldMap from './WorldMap';
import Pokedex from './Pokedex';
import {
  widthOfHpBarInPx,
  widthOfPokedexInPx,
  widthOfWorldMapInPx,
} from '../shareableCSS';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.state.isZoomed = false;
  }

  componentDidMount() {
    this.props.dispatch({type: 'SPAWN_PLAYER'});
  }

  render() {
    return (
      <div style={{
        margin: '10px auto',
        width: widthOfHpBarInPx + widthOfWorldMapInPx + widthOfPokedexInPx + 20 + 'px',
      }}>
        <Pokedex />
        <HPBar />
        <div
          style={{
            float: 'right',
            marginLeft: '10px',
            marginRight: '10px',
            width: widthOfWorldMapInPx + 'px',
          }}
        >
          <WorldMap
            layoutStyle={{}}
            isZoomed={this.state.isZoomed}
          />
          <div style={{clear: 'both'}} />
          <div style={{
            backgroundColor: '#fff',
            border: '1px solid #999',
            borderRadius: '4px',
            cursor: 'pointer',
            display: 'inline-block',
            marginTop: '10px',
            padding: '3px 7px',
          }}
          onClick={() => this.setState({isZoomed: !this.state.isZoomed})}
          >Zoom</div>
        </div>
      </div>
    );
  }
}

export default connect()(App);



